/**
	|| Defaults 
**/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/**
  || Playing Card
**/

:root {
  background-color: gray;
  padding: 100px;
}

.card {
  background-color: white;
  border: 1px solid white;
  border-radius: 10px;
  height: 200px;
  width: 142px;
  padding: .8em;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  color: darkred;
  filter: drop-shadow(0px 0px 5px #333);
  cursor: pointer;
}

.suit {
  text-align: center;
  font-size: 4rem;
  width: 100%;
}

.which {
  font-size: 1.3rem;
  line-height: 1;
}

.which span {
  display: block;
}

.which-reversed {
  transform: rotate(180deg);
  text-align: left;
  width: 100%;
}

.unicard {
  font-size: 150px;
  background-color: white;
}

.card:hover {
  filter: drop-shadow(0 0 20px #bbb);
}